:host ::ng-deep .ui-multiselected-empty-token {
  padding: 2px 4px;
  margin: 0 0.286em 0 0;
  display: inline-block;
  vertical-align: middle;
  height: 1.857em;
}
.color-display {
  width: 20px;
  height: 20px;
  border: 1px solid #ccc;
  display: inline-block;
  cursor: default;
}
#loading-information {
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  font-size: 11px;
  font-style: normal;
  font-variant: normal;
  font-weight: 400;
  line-height: 23px;
  padding: 10px;
  white-space: pre-line;
}
/* Search */
.autocomplete-wrapper {
  position: relative;
  /*border: solid 1px #EEE;*/
  padding: 1px 2px;
  font-family: 'Verdana', sans-serif;
}
.autocomplete-wrapper #search {
  padding: 0px 0px 0px 5px;
  margin: 0;
  width: 100%;
  font-size: 13px;
}
.autocomplete-wrapper #search:focus {
  outline: 0;
}
.autocomplete-wrapper #search-results {
  display: none;
  position: absolute;
  z-index: 5;
  top: 36px;
  left: -1px;
  right: -1px;
  border: solid 1px #CCC;
  min-height: 50px;
  max-height: 200px;
  overflow-y: scroll;
  list-style: none;
  padding: 2px;
  background: white;
}
.autocomplete-wrapper #search-results li {
  padding: 5px;
  margin: 0;
  font-size: 13px;
}
.autocomplete-wrapper #search-results li:hover {
  background: #EEE;
  cursor: pointer;
}
#url-warning-div {
  bottom: 0;
  position: absolute;
  width: 100%;
  z-index: 10;
  background-color: #f00;
  color: #fff;
  font-weight: 600;
  font-size: 14px;
  text-align: center;
}
#url-warning-div a {
  color: #fff;
}
#search-form {
  display: none;
  float: right;
  z-index: 2;
  position: relative;
  margin-left: auto;
}
label[for="search"] {
  color: white;
  margin-right: 10px;
}
#search {
  border-radius: 0.25rem 0 0 0.25rem;
}
#search-field {
  border-radius: 0 0.25rem 0.25rem 0;
  border-left: 0;
  margin-left: 2px;
}
#casesensitivebutton,
#wholewordbutton {
  margin: 2px;
  border-radius: 0.25rem;
}
:host ::ng-deep .ui-multiselected-item-token {
  background: #007ad9;
  color: #ffffff;
}
:host ::ng-deep .ui-multiselected-empty-token {
  background: #d95f00;
  color: #ffffff;
}
.tab-content {
  padding-top: 15px;
}
.loader {
  position: absolute;
  left: calc(50% - 20px);
  top: 40%;
}
.pointer {
  cursor: pointer;
}
.about-links {
  padding: 10px 0 10px 0;
}
.tabulator {
  font-size: 12px;
  border: none;
}
.tabulator .tabulator-header {
  font-weight: normal;
  border-bottom: 1px solid;
  border-bottom-color: rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.54);
}
.tabulator .tabulator-header .tabulator-col {
  height: 48px;
  background-color: #ffffff;
  border: none;
  padding: 0 7px;
}
.tabulator-row {
  border-bottom: 1px solid;
  border-bottom-color: rgba(0, 0, 0, 0.12);
  min-height: 48px;
  vertical-align: middle;
}
.tabulator-row .tabulator-cell {
  padding: 16px 7px;
  border-right: none;
  vertical-align: middle;
}
.tabulator-row .tabulator-cell:first-of-type,
.tabulator .tabulator-header .tabulator-col:first-of-type {
  padding-left: 24px;
}
.tabulator-row.tabulator-row-even {
  background-color: #ffffff;
}
/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
  /* If you want dots under the hoverable text */
}
/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}
/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}
.floater {
  border: 1px solid #cccccc;
  border-radius: 0.25rem;
}
#color-transparency-wrapper {
  display: none;
  position: absolute;
  z-index: 1100;
  border-radius: 0.25rem;
  color: #303030;
  background-color: #f8f9fa !important;
  padding: 4px 8px;
  transform: rotate(270deg);
  transform-origin: top left;
}
.mt-icon-toolbar {
  width: 26px;
  height: 26px;
  margin-left: 5px;
}
.transparency-symbol {
  cursor: pointer;
}
#network-statistics-wrapper {
  display: none;
  position: absolute;
  bottom: 10px;
  right: 20px;
  z-index: 1;
  color: #303030;
  background-color: #f8f9fa !important;
  border: 1px solid #c0c0c0;
  border-radius: 0.25rem;
}
#network-statistics-draghandle {
  display: none;
  width: 100%;
  text-align: center;
}
#network-statistics-table td {
  border: none;
  padding: 0.125rem 0.25rem;
}
mat-slider {
  width: 300px;
}
#reveal-all {
  background-color: #dadada;
}
.custom-file-label {
  margin-left: 15px;
  margin-right: 15px;
}
#timeline {
  padding-top: 50px;
}
#timeline .axis--grid .domain {
  fill: #ddd;
  stroke: none;
}
#timeline .axis--x .domain,
#timeline .axis--grid .tick line {
  stroke: #fff;
}
#timeline .axis--grid .tick--minor line {
  stroke-opacity: 0.5;
}
#timeline-date-field {
  width: auto;
}
#global-timeline-wrapper {
  /* background: #f8f9fa; */
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  position: absolute;
  left: 0px;
  bottom: 0px;
  right: 15px;
  z-index: 1001;
}
#timeline-play-button {
  position: absolute;
  top: 10px;
  left: 10px;
  background: #f08080;
  padding-right: 10px;
  border-radius: 3px;
  border: none;
  color: white;
  margin: 0;
  padding: 0 12px;
  width: 60px;
  cursor: pointer;
  height: 30px;
}
#global-timeline-field {
  padding: 4px;
  font-size: 1.25em;
}
#timeline-play:hover {
  background-color: #696969;
}
.track,
.track-inset,
.track-overlay {
  stroke-linecap: round;
}
.track {
  stroke: #000;
  stroke-opacity: 0.3;
  stroke-width: 10px;
}
.track-inset {
  stroke: #dcdcdc;
  stroke-width: 8px;
}
.track-overlay {
  pointer-events: stroke;
  stroke-width: 50px;
  stroke: transparent;
}
.handle {
  fill: #fff;
  stroke: #000;
  cursor: grab;
  stroke-opacity: 0.5;
  stroke-width: 1.25px;
}
.p-dialog .p-dialog-header button {
  color: #495057;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.p-dialog .p-dialog-header button:enabled:hover {
  border-color: transparent;
  background: #e9ecef;
}
#nodeColorCounts,
#nodeColorFrequencies,
#linkColorCounts,
#linkColorFrequencies {
  width: 100%;
  border-radius: 0;
  display: flex;
  justify-content: space-between;
  gap: 15px;
}
input[type="checkbox"] + label {
  margin-left: 8px;
}
label.disabled {
  color: gray;
}
.p-confirmdialog {
  width: 600px !important;
}
.p-confirmdialog .p-dialog-header {
  height: 24px !important;
  padding: 4px !important;
  border-radius: 12px 12px 0px 0px;
}
